        <img src="../assets/barcode.png" alt="条形码" />
      </div>

      <!-- 二维码 -->
      <div class="qrcode">
        <img src="../assets/qrcode.png" alt="二维码" />
      </div>

      <!-- 底部标签 -->
      <div class="card-footer">
        <div class="footer-item active">医保码</div>
        <div class="footer-item">刷新二维码</div>
      </div>
    </div>

    <!-- 底部导航栏 -->
    <div class="bottom-nav">
      <div class="nav-item" @click="goToHomePage">
